<template lang="html">
    <div class="business">
        <span class="upBtn" @click="openDialog"></span>
        <div class="mess_show" v-show="showMessBox">
          <!-- <div class="mess_title"></div> -->
          <div class="mess_con">
              <div class="item">
                <span></span><span>{{storeEditDate.licenseNumber}}</span>
              </div>
              <div class="item">
                <span></span><span>{{storeEditDate.registeredCapital}}</span>
              </div>
              <div class="item">
                <span></span><span>{{storeEditDate.time}}</span>
              </div>
              <div class="item">
                <span></span><span>{{storeEditDate.createTime}}</span>
              </div>
              <div class="item">
                <span></span><span>{{storeEditDate.legalPerson}}</span>
              </div>
              <div class="itemHeight">
                <span></span><span>{{storeEditDate.businessAddress}}</span>
              </div>
              <div class="itemHeight">
                <span></span><span>{{storeEditDate.organizationCode}}</span>
              </div>
              <div class="item" v-if="storeEditDate.custName">
                <span></span><span>{{storeEditDate.custName}}</span>
              </div>
              <div class="item itemLH">
                <span></span><span class="salve" @click="showPic"></span>
              </div>
          </div>
        </div>
        <Modal class="businessDialog"
            v-model="modal1"
            title=""
           >
           <Form ref="uploadBusi" :model="uploadBusi" :rules="checkValue" :label-width="130">
              <Form-item label=":" prop="licenseNumber">
                <Input v-model="uploadBusi.licenseNumber" placeholder="" class='fl'>
                </Input>
              </Form-item>
              <Form-item label=":" prop="registeredCapital">
                <Input v-model="uploadBusi.registeredCapital" placeholder="" class='fl'></Input>
                <span class="ML5 fl"></span>
                <span v-show="judgeErr.reg_cap_err_show" class="colorRed ML5">
                  {{errorCon.reg_cap_err}}
                </span>
              </Form-item>
              <div class="cycle">
                <Form-item label=":" prop="beginTime" class='dateInput'>
                  <Form-item prop="beginTime">
                    <Date-picker type="date" placeholder=""  v-model="uploadBusi.beginTime"
                    :editable="false"></Date-picker>
                  </Form-item>
                  <span class="space">-</span>
                  <Form-item prop="endTime">
                    <Date-picker type="date" placeholder=""  v-model="uploadBusi.endTime" 
                    :editable="false" @on-change="endDateChange"></Date-picker>
                  </Form-item>
                  <Checkbox v-model="forever" @on-change="foreverChange">
                    
                  </Checkbox>
                  <span class="colorRed">{{errorCon.dateErr}}</span>
                </Form-item>
              </div>
              <Form-item label=":" prop="createTime">
               <Date-picker type="date" placeholder="" class='fl' v-model="uploadBusi.createTime" :editable="false"></Date-picker>
              </Form-item>
              <Form-item label=":" prop="legalPerson">
                <Input v-model="uploadBusi.legalPerson" placeholder="" class='fl'></Input>
              </Form-item>
              <Form-item label=":" prop="businessAddress">
                <Input v-model="uploadBusi.businessAddress" placeholder="" class='fl'></Input>
              </Form-item>
              <Form-item label=":" prop="organizationCode">
                <Input v-model="uploadBusi.organizationCode" placeholder="" class='fl'></Input>
              </Form-item>
              <div class="upload">
                <span class="label">:</span>
                <Upload 
                  action="/api/isp-kongming/cust/imgUpdate"
                  :format="['jpg','jpeg','png']"
                  :max-size="10240"
                  :on-format-error="handleFormatError"
                  :on-exceeded-size="handleMaxSize"
                  :on-success="fileUploadSuccess"
                  >
                  <Button type="ghost" class="btn bg4373F3"></Button>
                  <span  class="ML15">1M</span> 
                </Upload>
                <div class="uperror" v-show="uploadImg.show">
                  <span>{{uploadImg.name}}</span><span class="del" @click="removeImg"></span>
                </div>   
                <span v-if="judgeErr.uploadErrShow"  class="colorRed uperror">{{errorCon.uploadErr}}</span>
              </div> 
              <Form-item>
                <Button type="primary" class="btn bg4373F3" @click="submit('uploadBusi')"></Button >
                <Button type="primary" class="btn bgCancle ML15" @click="cancel('uploadBusi')"></Button>
              </Form-item>              
            </Form>
            <div slot="footer" class="footer">   
            </div> 
        </Modal>
    </div>
</template>

<script>
export default {
  props:['editData'],
  data () {
    return {
      showMessBox:false,
      modal1: false,
      forever:false,
      uploadBusi:{
        licenseNumber:"",//
        registeredCapital:"",//
        beginTime:"",//  
        endTime:"",//  
        createTime:"",//
        legalPerson:"",//
        businessAddress:"",//
        organizationCode:"",//
        salve:"",//
      },
      checkValue:{
        licenseNumber: [
          {required: true, message:'',trigger:'blur'},
          { type: 'string', max: 30, message: '30', trigger: 'blur'}
        ],
        registeredCapital: [
          {required:true,message:'',trigger:'blur'},
          { type:'string', max:10, message: '10', trigger: 'blur'}
        ],
        legalPerson: [
          {required:true,message:'',trigger:'blur',type:"string"},
          { type: 'string', max:10, message: '10', trigger: 'blur'}
        ],
        businessAddress:[{required:true,message:'',trigger: 'blur',type:"string"}],
        beginTime: [{required:true,message:'',trigger:'change',type:"date"}],
        createTime: [{required: true,message:'',trigger:'change',type:"date"}],
      },
      judgeErr:{
        uploadErrShow:false,
        reg_cap_err_show:false
      },
      errorCon:{
        dateErr:"",
        uploadErr:"",
        reg_cap_err:""
      },
      brandIdOption:[],
      storeEditDate:{
        custId:"",//id
        licenseNumber: "",//
        registeredCapital: "",//
        time: "",//
        beginTime:"",
        endTime:"",
        createTime: "",//
        legalPerson: "",//
        businessAddress: "",//
        organizationCode: "",//
        custName: "",//
        salve: ""//
      },
      uploadImg:{
        name:"111",
        show:false
      }
    }
  },
  created(){
    this.storeEditDate=this.editData
    if(this.storeEditDate.licenseNumber){
      this.showMessBox=true
    }
  },
  methods: {
      removeImg(){
        this.uploadImg.name="";
        this.uploadImg.show=false
      },
      endDateChange(data){//
        if(data!=""&&this.forever){
          this.forever=false
        }
      },
      foreverChange(data){//
        console.log(data)
        if(data&&this.uploadBusi.endTime!=""){
          this.uploadBusi.endTime=""
        }
      },
      showPic(){
        this.$emit('showPic',"pic2")
      },
      openDialog(){
        this.modal1=true      
        if(this.storeEditDate.licenseNumber){//
          this.uploadBusi.registeredCapital=this.storeEditDate.registeredCapital
          this.uploadBusi.beginTime=new Date(this.storeEditDate.beginTime)
          if(this.storeEditDate.endTime==""){
            this.forever=true
            this.uploadBusi.endTime=""
          }else if(this.storeEditDate.endTime!=""){
            this.uploadBusi.endTime=new Date(this.storeEditDate.endTime)
          }        
          this.uploadBusi.licenseNumber=this.storeEditDate.licenseNumber;
          this.uploadBusi.createTime=new Date(this.storeEditDate.createTime)
          this.uploadBusi.legalPerson=this.storeEditDate.legalPerson
          this.uploadBusi.businessAddress=this.storeEditDate.businessAddress
          this.uploadBusi.organizationCode=this.storeEditDate.organizationCode
        }
      },
      submit (name) {
        this.$refs[name].validate((valid) => {
            if (valid) {
                let check_result=this.valueCheck()//
                let data_check=this.dateChange();//
                if(check_result&&data_check){
                  this.uploadBusi.custId=this.$router.currentRoute.query.id;
                  if(this.uploadBusi.endTime==""){
                    this.uploadBusi.endTime=""

                  }
                  this.$emit('uploadbus',this.uploadBusi,'upload')
                  this.modal1=false
                }               
            } else {
                let data_check=this.dateChange();//
                this.$Modal.error({
                    title: '',
                    content: ""
                })
            }
        })
      },
      cancel (name) {
        if(this.storeEditDate.licenseNumber){
          this.$refs[name].resetFields();
        }
        
        this.modal1=false
      },
      valueCheck(){
        //
        let reg_capital=/^\d*$/
        if(!this.uploadBusi.registeredCapital.match(reg_capital)){//
          this.judgeErr.reg_cap_err_show=true;
          this.errorCon.reg_cap_err=","
          return false
        }else if(this.uploadBusi.registeredCapital.match(reg_capital)){//
          this.judgeErr.reg_cap_err_show=false;
          this.errorCon.reg_cap_err=""
          return true
        }else{
          this.judgeErr.reg_cap_err_show=false;
          this.errorCon.reg_cap_err=""
          return true
        }
      },
      dateChange(){//
          if(this.uploadBusi.beginTime==""){
             this.errorCon.dateErr=""
             return false
          }
          if(!this.forever){
            if(this.uploadBusi.endTime==""){
              this.errorCon.dateErr=""
              return false
            }else{
              let bdateStamp=new Date(this.uploadBusi.beginTime)
              let edateStamp=new Date(this.uploadBusi.endTime)
              if(bdateStamp>edateStamp){
                this.errorCon.dateErr=""
                return false
              }
              else{
                this.errorCon.dateErr=""
                return true
              }
            }
          }
          this.errorCon.dateErr=""
          return true
      },
      handleFormatError(file){
         this.errorCon.uploadErr=' ' + file.name + '  jpg  png '
         this.judgeErr.uploadErrShow=true
      },
      handleMaxSize (file) {           
        this.errorCon.uploadErr=' ' + file.name + ' 1M'
        this.judgeErr.uploadErrShow=true
      },
      fileUploadSuccess(response, file, fileList){
        this.uploadImg.name="";
        this.uploadImg.show=true;
        this.uploadBrand.salve=""
      },
      formatTen(num) { 
        return num > 9 ? (num + "") : ("0" + num); 
      },
      formatDate(date) { // ->
        var year = date.getFullYear(); 
        var month = date.getMonth() + 1; 
        var day = date.getDate(); 
        var hour = date.getHours(); 
        var minute = date.getMinutes(); 
        var second = date.getSeconds(); 
        return year + "-" + this.formatTen(month) + "-" + this.formatTen(day); 
      }
  },
  watch:{
    editData:{
      handler:function(){
        this.storeEditDate=this.editData
        if(this.storeEditDate.licenseNumber){
          this.showMessBox=true
        }
      },
      deep:true
    }
  }

}
</script>

<style lang="scss">
.business{ 
  .mess_show{
      background: #F9FAFC;
      margin-top: 20px;
      width:400px;
      height: 240px;
      overflow: auto;
      border-radius: 2px;
      .mess_title{
        width: 100%;
        height: 40px;
        line-height: 40px;
        border-bottom:1px solid #ccc;
        text-align:center;
      }
      .salve{font-size:12px;color:blue;cursor:pointer}
      .mess_con{
        width: 100%;
        padding: 10px 20px;
        height: 100px;
        .item{
          width: 100%;
          float: left;
          height: 27px;
          line-height: 27px;
          span{line-height:27px}
        }
        .itemLH{line-height:10px;span{line-height:27px}}
        .itemHeight{
          width: 100%;
          float: left;
          overflow: hidden;
          span:first-child{height:27px;display:inline-block;float:left;line-height:27px}
          span:last-child{display:inline-block;float:left;width:240px;line-height:20px;margin-top:3px}
        }
      }
  }
}
.upBtn{
  text-align: center; 
  display: inline-block;
  width: 120px !important;
  height: 38px !important;
  background-color:white !important;
  border: 1px solid #4373F3;
  line-height: 38px !important;
  border-radius:2px;
  font-size: 14px;
  color: #4373F3;
  cursor: pointer
}
.businessDialog {  
   .ivu-upload-list{display:none}
    display: inline-block; 
    .ivu-input-type,input{width:350px}
    .dateInput {
      .ivu-form-item{display:inline-block;width:174px !important;float: left
        space{display:inline-block;float:left}
      }
      .ivu-input-type,input{width:164px}
    }
    .upload{
      width: 100%;
      padding-bottom: 20px;
      .label{
        display:inline-block;
        width:116px;
        height: 38px;
        line-height: 38px;
        text-align:right;
        padding: 10px 12px 10px 0;
        &::before {
          content: '*';
          display: inline-block;
          margin-right: 4px;
          line-height: 1;
          font-family: SimSun;
          font-size: 12px;
          color: #ed3f14;
        }
      }
      .ivu-upload{
        display:inline-block;
      }
      .uperror{ 
        padding-left:117px;width:100%;display:block;margin-top:10px;
        .del{color:#4373F3;margin-left:200px;cursor:pointer}
      }
    }
    .ivu-modal{width:700px !important; height:500px;overflow:auto;top:30px} 
    .footer{text-align:center;}
}

</style>



