<template lang="html">
  <div class="map">
    <span  class="upBtn" @click="open"></span>
    <div id="litter_map" v-show="litter_map_show">
    </div>
    <Modal class="mapDialog"
        v-model="modal1"
        title=""
       >
       <div></div>
      <div class="search">
        <input type="" name="" v-model="keyWord" placeholder="">
        <span class="searchBtn" @click="searchLocaton"></span>
      </div>
      <div id="allmap"></div>
      <span v-show="no_sign_error_show" class="colorRed ML5 MT10">
        ,
      </span>
      <div class="MT20">
        <Button type="primary" class="btn bg4373F3" @click="submit"></Button >
        <Button type="primary" class="btn bgCancle ML15" @click="cancel"></Button>
      </div>
      
      <div slot="footer" class="footer">   
      </div>
    </Modal>
  </div>
</template>

<script>
export default {
  props:['location'],
  data () {
    return {
      litter_map_show:false,
      no_sign_error_show:false,
      modal1:false,
      keyWord:'',//
      mapObj:null,//
      map:null,
      mapStatus: false,//
      signLocation:{//
        lng:null,
        lat:null
      }
    }
  },
  created(){
    this.signLocation=this.location
    this.showLitterMaps()
  },
  methods: {
    open(){//
      this.modal1=true
      if (!this.mapStatus) {// API
        document.getElementById('allmap').innerHTML=""
        document.getElementById('allmap').style.width="600px"
        document.getElementById('allmap').style.height="400px"
        setTimeout(()=>{
            var map = new BMap.Map("allmap");    // Map
            this.map=map;
            map.centerAndZoom(new BMap.Point(116.404, 39.915), 11);  // ,
            map.addControl(new BMap.MapTypeControl());   //
            map.setCurrentCity("");          //  
            this.mapObj = new BMap.LocalSearch(map, {
              renderOptions:{map: map}
            });
            map.addEventListener("click", this.showInfo);
            if(this.signLocation.lng==null){
              this.mapObj.search("");
            }else{
              var new_point = new BMap.Point(this.signLocation.lng,this.signLocation.lat);
              var marker = new BMap.Marker(new_point);  // 
              map.addOverlay(marker);              // 
              map.panTo(new_point); 
            }
            
            map.enableScrollWheelZoom(true);     //
            this.mapStatus = true;
        },0)
      }
    },
    searchLocaton(){
      if(this.mapObj!=null){
        this.mapObj.search(this.keyWord);
      }
    },
    showInfo(e){
      var new_point = new BMap.Point(e.point.lng,e.point.lat);
      var marker = new BMap.Marker(new_point);  // 
      this.map.addOverlay(marker);              // 
      this.map.panTo(new_point); 
      this.signLocation.lng=e.point.lng;
      this.signLocation.lat=e.point.lat;
    },
    submit () {//
      if(this.signLocation!=null){
        this.showLitterMaps()
        this.modal1=false
        this.$emit('stroe',this.signLocation);
      }else{
        this.no_sign_error_show=true
      }
    },
    cancel () {
      this.modal1=false
    },
    showLitterMaps(){//
      if(this.signLocation.lng==null){
        this.litter_map_show=false;
      }else{
        this.litter_map_show=true;
        setTimeout(()=>{
          document.getElementById('litter_map').innerHTML=""
          document.getElementById('litter_map').style.width="300px"
          document.getElementById('litter_map').style.height="200px"
        },0)
        
        setTimeout(()=>{
          var litterMap = new BMap.Map("litter_map");    // Map
          litterMap.centerAndZoom(new BMap.Point(116.404, 39.915), 11);  // ,
          litterMap.addControl(new BMap.MapTypeControl());   //
          litterMap.setCurrentCity("");          //  
          litterMap.enableScrollWheelZoom(true);     //
          litterMap.clearOverlays(); 
          var new_point = new BMap.Point(this.signLocation.lng,this.signLocation.lat);
          var marker = new BMap.Marker(new_point);  // 
          litterMap.addOverlay(marker);              // 
          litterMap.panTo(new_point); 
        },10)
       
        
      }
    }
  },
  watch:{
    location:{
      handler:function(){
        this.signLocation=this.location;
        this.showLitterMaps()
      }
    }
  }
}
</script>

<style lang="scss">
.mapDialog{
  .ivu-modal{width:700px !important; height:500px;top:20px} 
  #allmap{width:600px;height:400px;margin-bottom:10px}
  .search{
    width: 100%;
    height: 50px;
    input{
      width:300px;height:38px;
      border:1px solid #ccc;
      float: left;
      text-indent:10px
    }
    span.searchBtn{
      float: left;
      width: 50px;
      height: 38px;
      line-height: 38px;
      background: #3C6FFB !important;
      color: white;
      text-align: center;
      cursor:pointer;
    }
  }
}
.upBtn{
  text-align: center; 
  display: inline-block;
  width: 120px !important;
  height: 38px !important;
  background-color:white !important;
  border: 1px solid #4373F3;
  line-height: 38px !important;
  border-radius:2px;
  font-size: 14px;
  color: #4373F3;
  cursor: pointer
}
.map{
  #litter_map{width:300px;height:200px;border:1px solid #ccc;margin-top:20px}
}


</style>



