<script>
  import UploadBrand from 'components/createUser/UploadBrand';
  import UploadBusiness from 'components/createUser/uploadBusiness';
  import UploadPay from 'components/createUser/uploadPay';
  import DialogMap from 'components/createUser/map';
  import DialogArea from 'components/createUser/juristicArea';
  import config from './config.js';
  export default {
      components:{
        UploadBusiness,
        UploadBrand,
        UploadPay,
        DialogMap,
        DialogArea
      },
      data () {
        return {
          loginName:"",//
          uploadBrandLength:0,
          showSalve:false,
          imgPath:"",
          governArealist:[],
          selectedBrand:[],//
          clearObj:null,
          need:true,
          loading:{
            custPidLoad:true,//
            groupIdLoad:true,//
            foursIdLoad:true,//4s
            dealerIdLoad:true,//
            salenetLoad:true,//
            brandIdLoad:true,//
          },
          optionArr:{//
            provinceOption:[],//
            cityOption:[],//
            areaOption:[],//
            subclassIdOption:[//
              {name:"4s",value:1},
              {name:"",value:2},
              {name:"",value:3}
            ],
            industryOption:[//
              {name:"",value:10001},
              {name:"",value:10002},
              {name:"",value:10003},
              {name:"",value:10004}
            ],
            custPidOption:[],//
            groupIdOption:[],//
            foursIdOption:[],//4s
            dealerIdOption:[],//
            salenetOption:[
            {name:"1",value:1},
            {name:"2",value:2},
            {name:"3",value:3},
            {name:"4",value:4},
            {name:"5",value:5},
            ],//
            brandIdOption:[]//
          },
          showNeed:{//
            custName_show:"",//+ 
            typeId_show:"",//
            dealerId_show:"",//
            abbrName_show:"",// +         
            brandId_show:"",//+
            industryId_show:"",//+
            groupId_show:"",//+
            custPid_show:"",//+
            salenet_show:"",//
            GovernArealist_show:"",//
            subclassId_show:"",//
            foursId_show:"",//4s
            address_show:"",//
            map_show:"",//
            vendorCode_show:"",//
            companypro_show:"",//
            notes_show:"",//+
            provinceId_show:"",//+
          },
          formValidate:{//
            //
            custName:"",//+ 
            typeId:"",//
            abbrName:"",// +         

            brandId:[],//id+
            brandName:[],//
            custBrandMapList:[],//id
            industryId:"",//id+
            groupId:"",//+
            custPid:"",//+
            dealerId:"",//
            salenet:"",//

            subclassId:"",//
            foursId:"",//4s
            address:"",//
            // map:"",//
            longitude:"",//
            latitude:"",//

            vendorCode:"",//
            companypro:"",//
            notes:"",//+
            provinceId:"",//+
            cityId:"",//+
            countyId:"",//+
            createUser:"",
            logName:""
          },
          ruleValidate: {//
            custName: [
              {required: true, message:'',trigger:'blur'},
              {max:50, message:'50',trigger:'blur'}
            ],
            typeId:[
              {required: true, message:'',trigger:'change',type:"number"}
            ],
            abbrName:[
              {required: true, message:'',trigger:'blur'},
              {max:15, message:'15',trigger:'blur'}
            ],
            industryId:[
              {required: true, message:'',trigger:'change',type:"number"},
            ],
            brandId:[
              {required: true, message:'',trigger:'change',type:"array"},
            ],
            provinceId:[
              {required: true, message:' ',trigger:'change',type:"number"},
            ],
            subclassId:[
              {required: true, message:'',trigger:'change',type:"number"},
            ],
            address:[
              {required: true, message:'',trigger:'blur'},
              {max:50, message:'50',trigger:'blur'}
            ],
            salenet:[
              {required: false}
            ],
            vendorCode:[
            {max:20, message:'20',trigger:'blur'}
            ],  
            companypro:[
            {max:500, message:'500',trigger:'blur'}
            ], 
            notes:[
            {max:200, message:'200',trigger:'blur'}
            ]      
          },
          judgeShow:{//
            submitClick:false,//
            submitAuditClick:false,
            custNameErrShow:false,//
            areaErrShow:false,//
            abbrName_err_show:false,//
            GovernArealist_err_show:false,//
            mapErr_show:false,//
            showUpload:true
          },
          errorMess:{//
            custNameErr:",",//
            areaErr:"",
            abbrName_err:"",
            GovernArealist_err:"",
            mapErr:""//
          },
          //
          mapLocation:{//
            store_4s:{
              lng:null,
              lat:null
            },
            store_colligate:{
              lng:null,
              lat:null
            },
            store_agency:{
              lng:null,
              lat:null
            }
          },
          checkedAreaList:[//
          ],
          uploadBusiObj:{//
          },
          submitBusiObj:{},
          uploadBrandArr:[//
          ],
          uploadPayObj:{//
            taxCode:"",
            custBankAccountList:[],
            salve:"",
            validTime:"",
            status:"",
          },
          submitPayObj:{},
          addressObj:{
            provinceId:"",
            cityId:"",
            countyId:""
          },
          isUpload:{
            busi:"",
            brand:"",
            bank:""
          }
        }
      },
      created() {//
        let id=this.$router.currentRoute.query.id
        if(!id){//
          this.setSelectOption("","","","")//
          this.getBrandOption([],[])//
        }
        /**********************/
        this.$http.get(config.urlList.getArea+"?pId=-1&pageSize=32").then((res)=>{//
          if(res.data.errorCode===0){
             this.optionArr.provinceOption=res.data.result
          }
          else {
            this.$Modal.info({
                title: '',
                content: res.data.errorMsg
            });
          }
          }).catch((res)=>{
        })
        this.$http.get(config.urlList.login).then((res)=>{//
          if(res.data.errorCode===0){
            this.loginName=res.data.result.username;
          }
          else {
            this.$Modal.info({
                title: '',
                content: res.data.errorMsg
            });
          }
        }).catch((res)=>{})
        /****************/
        setTimeout(()=>{
          if(id){
            this.$http.post(config.urlList.selPCCustInfoList,
            {custId:this.$router.currentRoute.query.id}
            ).then((res)=>{
              if(res.data.errorCode===0){
                this.edit(res.data.result[0])
                let groupName=res.data.result[0].groupName?res.data.result[0].groupName:""
                let custPName=res.data.result[0].custPName?res.data.result[0].custPName:""
                let dealerName=res.data.result[0].dealerName?res.data.result[0].dealerName:""
                let foursName=res.data.result[0].foursName?res.data.result[0].foursName:""
                let brandnameArr=[];
                let brandidArr=[];
                for(let i=0;i<res.data.result[0].custBrandMapList.length;i++){
                  brandnameArr.push(res.data.result[0].custBrandMapList[i].brandName)
                  brandidArr.push(res.data.result[0].custBrandMapList[i].brandId)
                }
                this.setSelectOption(groupName,custPName,foursName,dealerName)
                this.getBrandOption(brandnameArr,brandidArr)

              }
              else {
                this.$Modal.info({
                    title: '',
                    content: res.data.errorMsg
                });
              }
            }).catch((res)=>{})
          }
        },0)
      },
      methods:{
        showPicDialog(data){
          this.showSalve=true
        },
        /**************/
        getCheckArea(data){// 
          this.checkedAreaList=data;
          if(this.checkedAreaList.length>=1){
            this.judgeShow.GovernArealist_err_show=false;
            this.errorMess.GovernArealist_err=""
          }
          //governArealist
          var arr=[]
          for(let i=0;i<this.checkedAreaList.length;i++){
            let obj={}
            if(this.checkedAreaList[i].status==true){
              obj.name=this.checkedAreaList[i].name;
              obj.id=this.checkedAreaList[i].id;
              obj.status=true;
              arr.push(obj)
            }else if(this.checkedAreaList[i].status==false){
              obj.name=this.checkedAreaList[i].name;
              obj.id=this.checkedAreaList[i].id;
              let children=this.checkedAreaList[i].children
              obj.status=false;
              let child=[]
              for(let z=0;z<children.length;z++){
                let childObj={}
                childObj['name']=children[z].name
                childObj['id']=children[z].value
                child.push(childObj)
              }

              obj.children=child
              arr.push(obj)
            }
          }
          this.governArealist=arr;
        },
        getFourSLoation(location){// 4s
          this.mapLocation.store_4s=location
        },
        getColligateLoation(location){// 
          this.mapLocation.store_colligate=location
        },
        getAgencyLoation(location){// 
          this.mapLocation.store_agency=location
        },
        getUploadBusiness(data,key){// 
          this.isUpload.busi=key
          let obj={}
          let obj1={}
          for(let i in data){
            obj[i]=data[i]
          }
          if((typeof obj.createTime)=='string'){
            obj.createTime=obj.createTime.substring(0,10)
            obj.beginTime=obj.beginTime.substring(0,10)
            if (obj.endTime!="") {
              obj.endTime=obj.endTime.substring(0,10)
            }
          }else{
            obj.createTime=this.formatDate(obj.createTime)
            obj.beginTime=this.formatDate(obj.beginTime)
            if (obj.endTime!="") {
              obj.endTime=this.formatDate(obj.endTime)
            }
          }
          obj.time=obj.forever?obj.beginTime+"":obj.beginTime+""+obj.endTime

          obj1['licenseNumber']=data.licenseNumber
          obj1['registeredCapital']=data.registeredCapital
          obj1['createTime']=obj.createTime
          obj1['beginTime']=obj.beginTime
          obj1['endTime']=obj.endTime
          obj1['legalPerson']=data.legalPerson
          obj1['businessAddress']=data.businessAddress
          obj1['organizationCode']=data.organizationCode
          obj1['salve']=data.salve;
          this.submitBusiObj=obj1;
          this.uploadBusiObj=obj
        },
        getUploadBrand(data,key){// 
          this.isUpload.brand=key
          if(data instanceof Array){
            this.uploadBrandArr=data
            return;
          }
          let obj={}
          for(let i in data){
            if(i!="validTime"&&i!="createTime")
            obj[i]=data[i]
          }
          if((typeof obj['validTime'])=="string"){
            obj['validTime']=data.validTime.substring(0,10)
          }else{
            obj['validTime']=this.formatDate(data.validTime)
          }
          this.uploadBrandArr.push(obj)        
        },
        getUploadPay(data,key){// 
          this.isUpload.bank=key
          let obj={}
          this.uploadPayObj.taxCode=data.taxCode
          this.uploadPayObj.custBankAccountList=data.custBankAccountList
          this.uploadPayObj.salve=data.salve
          if(data.validTime){
