        toArr(str){//
          if(str==null){
            return [];
          }
          if(str.length==0){
            return []
          }else if(str.length==1){
            return [parseInt(str)]
          }else if(str.replace(/\,/g,"").match(/^(\d+)$/)){
            return  Array.from(str.split(","), (x) => parseInt(x))
          }else {
            return str.split(",")
          }
        },
        toStr(arr){//
          if(arr.length==0){
            return ""
          }else if(arr.length==1){
            return ''+arr[0]
          }else{
            return  arr.join(',')
          }
        }
